import React, { useState } from 'react';
import axios from 'axios';
import { Snackbar, Alert } from "@mui/material";

import { TextField,  Button } from "@mui/material";
import Autocomplete from '@mui/material/Autocomplete';

const EditCustomer = ({ customerData, handleClose }) => {
    const [formData, setFormData] = useState({
        firstName: customerData.first_name ? customerData.first_name : '',
        lastName: customerData.last_name ? customerData.last_name : '',
        email: customerData.user.email ? customerData.user.email : '',
        profileImage: customerData.profile_image ? customerData.profile_image : '',
        phoneNumber: customerData.phone_number ? customerData.phone_number : '',
        industryType: customerData.industry_type ? customerData.industry_type : ''
    });
    const [successAlert, setSuccessAlert] = useState(false);
    const [errorAlert, setErrorAlert] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');
    const apiUrl = process.env.REACT_APP_API_URL;

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setFormData({ ...formData, [name]: value });
    };

    const handleSubmit = async (event) => {
        const { firstName, lastName, email, industryType, phoneNumber, profileImage } = formData;

        event.preventDefault();
        try {
            const token = localStorage.getItem("token")
            const headers = {
                'x-sh-auth': token,
            };
            const response = await axios.put(`${apiUrl}api/customer/edit_customer_by_admin/${customerData.user._id}`, {
                first_name: firstName,
                last_name: lastName,
                email: email,
                industry_type: industryType,
                phone_number: phoneNumber,
                profile_image: profileImage
            }, { headers: headers });
            console.log('Customer added successfully:', response.data);
            handleClose();
            setSuccessAlert(true);
        } catch (error) {
            console.error('Error adding customer:', error.response);
            setErrorAlert(true);
            setErrorMessage(error.response.data.message);
        }
    };

    const handleCloseAlert = () => {
        setSuccessAlert(false);
        setErrorAlert(false);
        setErrorMessage('');
    };



    return (
        <>
            <form onSubmit={handleSubmit}>
                <div className="container">
                    <h1 style={{ marginBottom: "50px" }}>Edit Customer</h1>
                    <div className="row">
                        <div className="col-lg-6">
                            <TextField
                                sx={{
                                    '& label.Mui-focused': {
                                        color: '#00A95A',
                                    },
                                    '& .MuiOutlinedInput-root': {
                                        '&.Mui-focused fieldset': {
                                            borderColor: '#00A95A',
                                        },
                                    },
                                }}
                                fullWidth
                                name="firstName"
                                className="first-name"
                                id="outlined-basic"
                                label="First Name"
                                type="text"
                                variant="outlined"
                                value={formData.firstName}
                                onChange={handleInputChange}
                                required
                                style={{ marginBottom: '30px' }}
                            />
                            <TextField
                                sx={{
                                    '& label.Mui-focused': {
                                        color: '#00A95A',
                                    },
                                    '& .MuiOutlinedInput-root': {
                                        '&.Mui-focused fieldset': {
                                            borderColor: '#00A95A',
                                        },
                                    },
                                }}
                                fullWidth
                                name="email"
                                className="email"
                                id="outlined-basic"
                                label="Email"
                                type="email"
                                variant="outlined"
                                value={formData.email}
                                required
                                style={{ marginBottom: '30px' }}
                                readOnly

                            />
                            <Autocomplete
                                sx={{
                                    '& label.Mui-focused': {
                                        color: '#00A95A',
                                    },
                                    '& .MuiOutlinedInput-root': {
                                        '&.Mui-focused fieldset': {
                                            borderColor: '#00A95A',
                                        },
                                    },
                                }}
                                fullWidth
                                options={['Software', 'Healthcare', 'Education']}
                                renderInput={(params) => <TextField {...params} label="Industry Type" variant="outlined" />}
                                value={formData.industryType}
                                onChange={(event, newValue) => {
                                    setFormData({ ...formData, industryType: newValue });
                                }}
                                style={{ marginBottom: '30px' }}
                            />
                        </div>
                        <div className="col-lg-6">
                            <TextField
                                sx={{
                                    '& label.Mui-focused': {
                                        color: '#00A95A',
                                    },
                                    '& .MuiOutlinedInput-root': {
                                        '&.Mui-focused fieldset': {
                                            borderColor: '#00A95A',
                                        },
                                    },
                                }}
                                fullWidth
                                name='lastName'
                                className="last-name"
                                id="outlined-basic"
                                label="Last Name"
                                type="text"
                                variant="outlined"
                                value={formData.lastName}
                                onChange={handleInputChange}
                                required
                                style={{ marginBottom: '30px' }}
                            />
                            <TextField
                                sx={{
                                    '& label.Mui-focused': {
                                        color: '#00A95A',
                                    },
                                    '& .MuiOutlinedInput-root': {
                                        '&.Mui-focused fieldset': {
                                            borderColor: '#00A95A',
                                        },
                                    },
                                }}
                                fullWidth
                                name="profileImage"
                                className="profile-image"
                                id="outlined-basic"
                                label="profile-image"
                                type="text"
                                variant="outlined"
                                value={formData.profileImage}
                                onChange={handleInputChange}
                                required
                                style={{ marginBottom: '30px' }}
                            />
                            <TextField
                                sx={{
                                    '& label.Mui-focused': {
                                        color: '#00A95A',
                                    },
                                    '& .MuiOutlinedInput-root': {
                                        '&.Mui-focused fieldset': {
                                            borderColor: '#00A95A',
                                        },
                                    },
                                }}
                                fullWidth
                                name='phoneNumber'
                                className="phoneNumber"
                                id="outlined-basic"
                                label="phoneNumber"
                                type="number"
                                variant="outlined"
                                value={formData.phoneNumber}
                                onChange={handleInputChange}
                                required
                                style={{ marginBottom: '30px' }}
                            />
                        </div>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'flex-end', marginTop: '1rem' }}>
                        <Button variant="outlined" style={{ marginRight: '1rem', color: "#00A95A", border: "1px #00A95A" }} onClick={handleClose}>
                            Cancel
                        </Button>
                        <Button type="submit" variant="contained" style={{ backgroundColor: "#00A95A" }}>
                            Save
                        </Button>
                    </div>
                </div>
            </form>
            <Snackbar
                open={successAlert}
                autoHideDuration={6000}
                onClose={handleCloseAlert}
                anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
            >
                <Alert onClose={handleCloseAlert} severity="success" sx={{ backgroundColor: '#4caf50' }}>
                    Customer added successfully
                </Alert>
            </Snackbar>
            <Snackbar
                open={errorAlert}
                autoHideDuration={6000}
                onClose={handleCloseAlert}
                message={errorMessage}
                anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
            />
        </>
    );
}

export default EditCustomer;
